import React, { useState, useEffect } from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  Avatar,
  Div,
  Radio,
  Button,
} from "@vkontakte/vkui";

import { APIManager, sharing } from "../helpers";
const { callMethod } = APIManager;

const ModalBuy = ({
  setActiveModal,
  onChangeHandlerAmount,
  APP_AVATAR,
  getDonateLink,
  getUser,
  type,
}) => {
  const [sum, setSum] = useState(49);
  const [link, setLink] = useState("");

  useEffect(() => {
    callMethod("getDonateLink", {
      amount: type === "guard" ? 199 : sum,
    }).then(({ status, message }) => {
      // console.log(message);
      if (status === "success") {
        setLink(message);
        getUser();
      } else {
        // openAlert(message, "red");
        console.log(message);
      }
    });
  }, [sum]);

  return (
    <>
      {type === "buy" ? (
        <ModalRoot activeModal="buy">
          <ModalCard
            id="buy"
            onClose={() => setActiveModal(null)}
            icon={<Avatar size={72} src={APP_AVATAR} />}
            header="Выберите сумму пополнения"
            caption={
              <>
                <Div
                  style={{
                    display: "flex",
                    alignItems: "start",
                    justifyContent: "center",
                    flexDirection: "column",
                    width: "100%",
                  }}
                >
                  <Radio defaultChecked name="radio" onClick={() => setSum(49)}>
                    <Div
                      style={{
                        display: "flex",
                        alignItems: "start",
                        justifyContent: "center",
                        flexDirection: "column",
                        // width: "100%",
                      }}
                    >
                      <span
                        style={{
                          textDecoration: "line-through",
                          fontSize: "12px",
                          color: "red",
                          marginLeft: "30px",
                        }}
                      >
                        99 ₽
                      </span>
                      <span style={{ whiteSpace: "nowrap" }}>
                        49 ₽ ( 1 слежка )
                      </span>
                    </Div>
                  </Radio>
                  <Radio name="radio" onClick={() => setSum(119)}>
                    <Div
                      style={{
                        display: "flex",
                        alignItems: "start",
                        justifyContent: "center",
                        flexDirection: "column",
                      }}
                    >
                      <span
                        style={{
                          textDecoration: "line-through",
                          fontSize: "12px",
                          color: "red",
                          marginLeft: "30px",
                        }}
                      >
                        199 ₽
                      </span>
                      <span style={{ whiteSpace: "nowrap" }}>
                        119 ₽ ( 3 слежки )
                      </span>
                    </Div>
                  </Radio>
                  <Radio name="radio" onClick={() => setSum(189)}>
                    <Div
                      style={{
                        display: "flex",
                        alignItems: "start",
                        justifyContent: "center",
                        flexDirection: "column",
                      }}
                    >
                      <span
                        style={{
                          textDecoration: "line-through",
                          fontSize: "12px",
                          color: "red",
                          marginLeft: "30px",
                        }}
                      >
                        299 ₽
                      </span>
                      <span style={{ whiteSpace: "nowrap" }}>
                        189 ₽ ( 5 слежек )
                      </span>
                    </Div>
                  </Radio>
                </Div>
                <Div>
                  <Button
                    style={{
                      backgroundColor: "#184a6e",
                      color: "white",
                      borderRadius: "13px",
                    }}
                    size="xl"
                    level="2"
                    href={link}
                    target="_blank"
                    onClick={() => setActiveModal(null)}
                  >
                    Пополнить
                  </Button>
                </Div>
              </>
            }
          ></ModalCard>
        </ModalRoot>
      ) : (
        <ModalRoot activeModal="guard">
          <ModalCard
            id="guard"
            onClose={() => setActiveModal(null)}
            icon={<Avatar size={72} src={APP_AVATAR} />}
            header="Покупка защиты от слежки"
            caption={
              <>
                <Div
                  style={{
                    display: "flex",
                    alignItems: "start",
                    justifyContent: "center",
                    flexDirection: "column",
                    width: "100%",
                    padding: "20px 0px 20px 0px",
                  }}
                >
                  После покупки "Защиты от слежки" другие пользователи не смогут
                  узнать о Вас информацию.
                </Div>
                <Div>
                  <Button
                    style={{
                      backgroundColor: "#184a6e",
                      color: "white",
                      borderRadius: "13px",
                    }}
                    size="xl"
                    level="2"
                    href={link}
                    target="_blank"
                    onClick={() => setActiveModal(null)}
                  >
                    Купить
                  </Button>
                </Div>
              </>
            }
          ></ModalCard>
        </ModalRoot>
      )}
    </>
  );
};

export { ModalBuy };
